/* roboto-regular - latin */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src: url("fonts/roboto-v18-latin-regular.eot"); /* IE9 Compat Modes */
  src: local("Roboto"), local("Roboto-Regular"),
    url("fonts/roboto-v18-latin-regular.eot?#iefix") format("embedded-opentype"),
    /* IE6-IE8 */ url("fonts/roboto-v18-latin-regular.woff2") format("woff2"),
    /* Super Modern Browsers */ url("fonts/roboto-v18-latin-regular.woff")
      format("woff"),
    /* Modern Browsers */ url("fonts/roboto-v18-latin-regular.ttf")
      format("truetype"),
    /* Safari, Android, iOS */ url("fonts/roboto-v18-latin-regular.svg#Roboto")
      format("svg"); /* Legacy iOS */
}
/* roboto-700 - latin */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-display: swap;
  font-weight: 700;
  src: url("fonts/roboto-v18-latin-700.eot"); /* IE9 Compat Modes */
  src: local("Roboto Bold"), local("Roboto-Bold"),
    url("fonts/roboto-v18-latin-700.eot?#iefix") format("embedded-opentype"),
    /* IE6-IE8 */ url("fonts/roboto-v18-latin-700.woff2") format("woff2"),
    /* Super Modern Browsers */ url("fonts/roboto-v18-latin-700.woff")
      format("woff"),
    /* Modern Browsers */ url("fonts/roboto-v18-latin-700.ttf")
      format("truetype"),
    /* Safari, Android, iOS */ url("fonts/roboto-v18-latin-700.svg#Roboto")
      format("svg"); /* Legacy iOS */
}
/* roboto-900 - latin */
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-display: swap;
  font-weight: 900;
  src: url("fonts/roboto-v18-latin-900.eot"); /* IE9 Compat Modes */
  src: local("Roboto Black"), local("Roboto-Black"),
    url("fonts/roboto-v18-latin-900.eot?#iefix") format("embedded-opentype"),
    /* IE6-IE8 */ url("fonts/roboto-v18-latin-900.woff2") format("woff2"),
    /* Super Modern Browsers */ url("fonts/roboto-v18-latin-900.woff")
      format("woff"),
    /* Modern Browsers */ url("fonts/roboto-v18-latin-900.ttf")
      format("truetype"),
    /* Safari, Android, iOS */ url("fonts/roboto-v18-latin-900.svg#Roboto")
      format("svg"); /* Legacy iOS */
}

html {
  font-size: 62.5%; // 1rem = 10px, 3.5rem = 35px;
  font-family: "Roboto", sans-serif;
}

body {
  font-size: 1.6rem;
  line-height: 1.5;
  color: $text-primary;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 700;
  margin: 0;
}

h1 {
  font-size: 3.2rem;
  font-weight: 900;

  @include md {
    font-size: 4.8rem;
  }
}

h2 {
  font-size: 2.8rem;
  margin-bottom: 2.5rem;

  @include md {
    font-size: 3.2rem;
  }
}

h6 {
  font-size: 1.8rem;
}

p {
  font-size: 1.8rem;
  font-weight: 400;
  margin: 0;
}

a {
  text-decoration: none;
  color: $blue;
}
