$project-colors: (
  "reduction": #3f51b5,
  "themost": #00b41c,
  "karofilm": #162027,
  "schedule": #5269c4,
  "schedule-admin": #38498c,
  "newprovidence": #267df4,
  "mmio": #eee,
  "battlespace": #333,
  "vk": #4a76a8,
  "blueasy": #353f46,
  "minimo": #222f3e,
  "slider": #10ac84,
  "player": #3498db,
  "pictureperfect": #82ca9c,
  "signature": #0c2461,
  "asiamix": #062128
);

#projects {
  .project {
    display: flex;
    flex-direction: column;
    height: auto;
    min-height: 25rem;
    padding-top: 2.5rem;
    padding-bottom: 2.5rem;
    border-top: 1px solid $grey;
    width: 100%;

    &:first-of-type {
      margin-top: 2.5rem;
    }

    &-deprecated {
      .project__content-link,
      .project__preview-link {
        pointer-events: none;
      }

      .project__content-title,
      .project__content-description,
      .project__content-link,
      .project__preview-link {
        text-decoration: line-through;
        color: $deprecated-color;
      }

      .project__preview {
        background-color: $deprecated-color !important;
      }
    }

    @include sm {
      min-height: 35rem;
    }

    @include md {
      min-height: 28rem;
      flex-direction: row;
    }

    &__content,
    &__preview {
      @include md {
        flex: 50%;
      }
    }

    &__content {
      padding-bottom: 1.5rem;

      @include md {
        padding-bottom: 0;
        padding-right: 2rem;
      }

      &-title {
        font-size: 2.4rem;
        margin-bottom: 1.5rem;
      }

      &-description {
        margin-bottom: 1.5rem;
      }
    }

    &__preview {
      display: flex;
      justify-content: center;
      align-items: center;
      height: inherit;
      min-height: inherit;
      background-color: #ddd;
    }

    @each $name, $color in $project-colors {
      &-#{$name} {
        transition: background-color 300ms ease-in-out;
        background-color: $color !important;

        @include md {
          &:hover {
            background-color: darken($color, 10%) !important;
          }
        }
      }
    }
  }
}
